header {
  display: flex;
  flex: 1;
  align-items: center;
  padding: 10px 20px;
}
.text {
  text-align: center;
  max-width: 30em;
  padding: 15px;
  margin: 20px;
  line-height: 2em;
  /*font-size: 1.2em;*/
  border: 1px solid rgba(211, 211, 211, 0.3);
  border-radius: 10px;
}

.cleanser-logo {
  align-self: center;
  text-align: center;
  line-height: .3em;
  margin: 10px 20px;
  font-size: 2em;
  font-weight: bolder;
  color: red;
  transform: skew(-18deg) rotate(-5deg);
  /*-webkit-text-stroke: 1px #fcfcfc;*/
  /*paint-order: stroke fill;*/
}

.cleanser-logo > p {
  color: darkgray;
  font-size: .32em;
}

.segment {
  padding: 3px 0;
  margin: 2px 0;
}

.segment:hover {
  color: blue;
  cursor: pointer;
}

.segment.selected {
  color: orangered;
}

.segment.tagged {
  border: 1px solid lightblue;
  border-radius: 4px;

  padding: 3px 3px;
  margin: 2px 2px;
}

.tag {
  color: lightblue;
  user-select: none;
}

.search {
  display: flex;
  flex: 1;
}

.search-input {
  background-color: #f7f7f7;
  outline: none;
  border: 1px solid #f9f9f9;
  border-radius: 8px;
  padding: 10px 20px;
  margin: 20px;
  font-size: 1.2em;
  /*display: flex;*/
  flex: 1;
  max-width: 30em;
}